export default {
	data() {
		return {
			active: null,
			title: 'Hello',
			city: {
				from: '北京',
				to: '成都'
			},
			
			cyUserlist: [
			],
			
			hangbanTimelist: [
			],
			nowTime:'',// 所选航班日期
			ticdes:{},// 票务信息
			isLoading:true,// 是否在加载数据
		}
	},
	onLoad(opt) {
		this.ticdes = opt
		this.nowTime = opt.DepartDate
		this.setTopTimes()
		this.choseOneTic(this.hangbanTimelist[0],0)
		
	},
	methods: {
		
		// 设置周末数值展示
		setWeekStr(str){
			let week = ''
			switch (str){
				case 1:week = '周一';break;
				case 2:week = '周二';break;
				case 3:week = '周三';break;
				case 4:week = '周四';break;
				case 5:week = '周五';break;
				case 6:week = '周六';break;
				case 0:week = '周日';break;
			}
			return week
		},
		// 设置顶部日期滚动条选择不同时间
		setTopTimes(){
			// 设置跨月依然游刃有余 skr
			let time = this.nowTime
			let timeSeconds = new Date(time).getTime()
			let oneDate = {
			}
			let box = []
			// return
			// i 小于到数值就是展示多少条日期的数值
			for (let i = 0;i<4;i++) {
				let year = new Date(timeSeconds).getFullYear() // 获取到年
				let month = new Date(timeSeconds).getMonth() + 1 // 获取到月
				if (month < 10 ) {
					month = '0'+ month
				}
				let day = new Date(timeSeconds).getDate() // 获取到日期
				let showDay = month + '/' + day
				let week = new Date(timeSeconds).getDay() // 获取到周末
				 oneDate = {
					day:showDay,
					week:week
				}
				// if () {}
				box.push(oneDate)
				timeSeconds = timeSeconds + 86400000
			}
			// console.log(box)
			this.hangbanTimelist = box
		},
		
		
		// 选择某个日期的票
		choseOneTic(item, idx) {
			let itemO = this.hangbanTimelist[idx]
			this.active == idx ? this.active = null : this.active = idx
			let opt = this.ticdes
			let year = new Date(this.ticdes.DepartDate).getFullYear()
			opt.DepartDate = year+'/'+itemO.day
			this.getHangBanList(opt)
		},
		
		// 选择某张票不同类型
		gotoHangBanXZ(item,idx){
			let itemP = this.cyUserlist[idx]
			this.$store.commit('setFlyTic',itemP)
			uni.navigateTo({
				url:'../hangbanxuanze/hangbanxuanze?id='+'1'
			})
		},
		// 获取航班列表信息
		getHangBanList(opt){
			console.log(opt,'请求参数')
			// return
			// 获取信息的时候先加载一下刷新框
			// this.isLoading = true
			uni.showLoading({
				title: '加载中'
			})
			// return
			var _this = this
			this.cyUserlist = []
			var Timestamp = new Date().toLocaleString()
			//http://apitest.99263.com 测试接口
			// let url = 'http://apitest.99263.com'
			let url = '/api'
			uni.request({
				url:url+"/Flight/Query",
				method:"POST",
				data:{
					  "ApiKey": "b421b9a21075a359c09a36db79325d5b",
					  "Sign": "",
					  "Timestamp": Timestamp,
					  "Data": {
						 "IsChild":opt.IsChild,
						"DepartCityCode": opt.DepartCityCode,
						"ArriveCityCode": opt.ArriveCityCode,
						"DepartDate": opt.DepartDate
					  }
					},
				success:function(res){
					console.log(res.data,'飞机票列表')
					if (res.data.IsSuccess) {
					_this.cyUserlist = res.data.Data
					} else {
						uni.showModal({
							showCancel:false,
							title:'提示',
							content:'暂无符合数据'
						})
					}
					// _this.isLoading = false
					uni.hideLoading()// 加载成功
				}
				
			})
		}
				
	}
}
